import React, { useState } from 'react';
import axios from 'axios';
import { v4 as uuidv4 } from 'uuid';
import { GoogleMap, Marker, LoadScript } from '@react-google-maps/api';

const Organizer = () => {
  const [festival, setFestival] = useState({
    event_id: '',
    name: '',
    description: '',
    start_date: '',
    end_date: '',
    location: '',
  });

  const [errors, setErrors] = useState({
    name: '',
    description: '',
    start_date: '',
    end_date: '',
    location: '',
  });

  const [mapCenter] = useState({ lat: 49.2827, lng: -123.1207 });
  const [selectedLocation, setSelectedLocation] = useState(null);

  const handleChange = e => {
    const { name, value } = e.target;

    setFestival(prevState => ({
      ...prevState,
      [name]: value,
    }));

    setErrors(prevState => ({
      ...prevState,
      [name]: '',
    }));

    // 更新地图中心位置
    if (name === 'location') {
      setSelectedLocation(null); // 清空选定的位置
    }
  };

  const handleMapClick = event => {
    const { latLng } = event;
    const lat = latLng.lat();
    const lng = latLng.lng();
    setSelectedLocation({ lat, lng });

    // 发起请求，将坐标转换为地址
    axios
      .get(
        `https://maps.googleapis.com/maps/api/geocode/json?latlng=${lat},${lng}&key=AIzaSyDZ0Sr7v2cjwBc5AJKnw9uXS7uOGkRqrC4`
      )
      .then(response => {
        const { results } = response.data;
        if (results.length > 0) {
          const address = results[0].formatted_address;
          setFestival(prevState => ({
            ...prevState,
            location: address,
          }));
        }
      })
      .catch(error => {
        console.error(error);
      });
  };

  const handleSubmit = async e => {
    e.preventDefault();

    const newErrors = {
      name: '',
      description: '',
      start_date: '',
      end_date: '',
      location: '',
    };

    // 检查输入框是否为空
    if (!festival.name) {
      newErrors.name = 'Please enter the festival name';
    }
    if (!festival.description) {
      newErrors.description = 'Please enter the description';
    }
    if (!festival.start_date) {
      newErrors.start_date = 'Please enter the start date';
    }
    if (!festival.end_date) {
      newErrors.end_date = 'Please enter the end date';
    }
    if (!festival.location) {
      newErrors.location = 'Please enter the location';
    }

    // 检查是否有错误
    if (Object.values(newErrors).some(error => error !== '')) {
      setErrors(newErrors);
      return;
    }

    try {
      const newEvent = {
        event_id: uuidv4(),
        name: festival.name,
        description: festival.description,
        start_date: festival.start_date,
        end_date: festival.end_date,
        location: festival.location,
      };

      console.log(newEvent);

      const response = await axios.post('http://localhost:3001/api/events', newEvent);
      console.log(response.data); // 可选的，打印后端返回的数据
      // 清空表单数据
      setFestival({
        event_id: '',
        name: '',
        description: '',
        start_date: '',
        end_date: '',
        location: '',
      });
      setErrors({});
    } catch (error) {
      console.error(error);
      // 处理请求错误
    }
  };

  return (
    <div>
      <h1>Organizer: create festival</h1>
      <form onSubmit={handleSubmit} className="organizer_form">
        <label className="organizer_label">
          Festival Name:
          <input
            type="text"
            name="name"
            value={festival.name}
            onChange={handleChange}
            className="organizer_input"
          />
          {errors.name && <p className="error_message">{errors.name}</p>}
        </label>
        <label className="organizer_label">
          Description:
          <textarea
            name="description"
            value={festival.description}
            onChange={handleChange}
            className="organizer_input"
          />
          {errors.description && <p className="error_message">{errors.description}</p>}
        </label>
        <label className="organizer_label">
          Start Date:
          <input
            type="date"
            name="start_date"
            value={festival.start_date}
            onChange={handleChange}
            className="organizer_input"
          />
          {errors.start_date && <p className="error_message">{errors.start_date}</p>}
        </label>
        <label className="organizer_label">
          End Date:
          <input
            type="date"
            name="end_date"
            value={festival.end_date}
            onChange={handleChange}
            className="organizer_input"
          />
          {errors.end_date && <p className="error_message">{errors.end_date}</p>}
        </label>
        <label className="organizer_label">
          Location:
          <input
            type="text"
            name="location"
            value={festival.location}
            onChange={handleChange}
            className="organizer_input"
          />
          {errors.location && <p className="error_message error">{errors.location}</p>}
        </label>

        <LoadScript googleMapsApiKey="AIzaSyDZ0Sr7v2cjwBc5AJKnw9uXS7uOGkRqrC4">
          <GoogleMap
            mapContainerStyle={{ height: '400px', width: '100%' }}
            zoom={10}
            center={mapCenter}
            onClick={handleMapClick}
          >
            {selectedLocation && <Marker position={selectedLocation} />}
          </GoogleMap>
        </LoadScript>

        <button type="submit" className="organizer_button">
          Submit
        </button>
      </form>
    </div>
  );
};

export default Organizer;
